import makeCopy from './makeCopy';
import getCoordinates from './getCoordinates';


export default function colorSamePixelsFunction(frame, color, pixelSize, e) {
  const clone = makeCopy(frame);
  const startCoords = getCoordinates(e, pixelSize);
  const startColor = clone[startCoords.x][startCoords.y]
    ? clone[startCoords.x][startCoords.y].join() : undefined;

  clone.forEach((row, x) => {
    row.forEach((cell, y) => {
      if (cell) {
        const correntColor = clone[x][y] ? clone[x][y].join() : undefined;
        if (correntColor === startColor) clone[x][y] = color;
      }
    });
  });

  return clone;
}
